  display: none;
}

/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Lato", sans-serif;
  font-size: 1.6rem;
  line-height: 1.8;
}

p {
  color: #000000;
  font-size: 2rem;
}

.contenedor {
  width: 95%;
  max-width: 120rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img,
picture {
  width: 100%;
  display: block;
  height: auto;
}

h1, h2, h3 {
  font-family: "Lato", sans-serif;
  margin: 2.5rem 0;
  font-weight: 300;
  text-align: center;
}

h1 {
  font-size: 3.8rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 3rem;
}

h4 {
  font-size: 2.6rem;
}

.seccion {
  margin-top: 2.5rem;
  margin-bottom: 2.5rem;
}

.contenido-centrado {
  max-width: 800px;
}

.w-100 {
  width: 100%;
  background-color: #ff2929;
}

.dark {
  background-color: #333333;
}
.dark *,
.dark .legend {
  color: #FFFFFF;
}
.dark .iconos-caracteristicas img,
.dark .icono img {
  filter: invert(100%);
}
.dark .anuncio {
  background-color: #2b2b2b;
  border: none;
}
.dark .footer,
.dark .header {
  background-color: #1f1f1f;
}
.dark input, .dark textarea, .dark select, .dark option {
  background-color: #1f1f1f;
  color: #FFFFFF;
}
.dark input[type=submit] {
  background-color: #71B100;
  color: #FFFFFF;
}
.dark input[type=submit].boton-rojo-block {
  background-color: #ff2929;
  color: #FFFFFF;
}
.dark input[type=submit].boton-rojo-block:hover {
  background-color: #ff4343;
}

.boton-amarillo {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  display: inline;
  padding: 1rem;
  text-align: center;
  transition: background-color 0.3s;
  border: none;
}
.boton-amarillo:hover {
  transition: background-color 0.3s;
  background-color: #f69c1c;
  cursor: pointer;
}

.boton-verde {
  background-color: #71B100;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  display: inline;
  padding: 1rem;
  text-align: center;
  transition: background-color 0.3s;
  border: none;
}
.boton-verde:hover {
  transition: background-color 0.3s;
  background-color: #8bda00;
  cursor: pointer;
}

.boton-rojo-block {
  background-color: #ff2929;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  display: inline;
  padding: 1.5rem;
  text-align: center;
  transition: background-color 0.3s;
  border: none;
  display: block;
  margin-top: 1rem;
  width: 100%;
}
.boton-rojo-block:hover {
  transition: background-color 0.3s;
  background-color: #ff5252;
  cursor: pointer;
}

.boton-amarillo-block {
  background-color: #E08709;
  color: #FFFFFF;
  font-weight: bold;
  text-decoration: none;
  display: inline;
  padding: 1rem;
  text-align: center;
  transition: background-color 0.3s;
  border: none;
  display: block;
  margin-top: 1rem;
}
.boton-amarillo-block:hover {
  transition: background-color 0.3s;
  background-color: #f69c1c;
  cursor: pointer;
}

.header {
  background-color: #333333;
  padding: 2rem 0 2rem 0;
}
.header h1 {
  display: none;
}

.inicio {
  background-position: center center;
  background-repeat: no-repeat;
  background-size: cover;
}
.inicio .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .inicio .contenido-header {
    min-height: 60rem;
  }
}
.inicio h1 {
  display: block;
  color: #FFFFFF;
  text-align: left;
  font-weight: bold;
  width: 61%;
}

.webp .inicio {
  background-image: url("../img/header.webp");
}

.no-webp .inicio {
  background-image: url("../img/header.jpg");
}

.movil-menu {
  display: flex;
  justify-content: center;
}
.movil-menu .img-menu {
  width: 80%;
}

.footer {
  background-color: #333333;
  padding: 1.6666666667rem 0;
  margin-bottom: 0;
}
.footer .copy {
  color: #FFFFFF;
  text-align: center;
}
.footer .contenedor-footer {
  padding-top: 1.6666666667rem;
}

.derecha {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .derecha {
    flex-direction: row;
    justify-content: end;
  }
}

.barra {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
  align-items: center;
}
@media (min-width: 768px) {
  .barra {
    flex-direction: row;
  }
}

.navegacion {
  opacity: 1;
  transition: opacity 0.4s;
}
@media (min-width: 768px) {
  .navegacion {
    display: flex;
    gap: 2rem;
  }
}
.navegacion a {
  color: #FFFFFF;
  text-decoration: none;
  display: block;
  text-align: center;
}

.hide {
  transition: opacity 0.3s;
  opacity: 0;
}
@media (min-width: 768px) {
  .hide {
    display: flex;
    opacity: 1;
  }
}

.movil-menu {
  width: 15%;
}
@media (min-width: 768px) {
  .movil-menu {
    display: none;
  }
}

.dark-btn {
  filter: invert(100%);
  width: 10%;
  margin: 1rem 0;
}
@media (min-width: 768px) {
  .dark-btn {
    width: 5%;
    margin: 0 2rem;
  }
}
.dark-btn:hover {
  cursor: pointer;
}

.iconos-nosotros {
  text-align: center;
}
@media (min-width: 768px) {
  .iconos-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.iconos-nosotros img {
  height: 10rem;
}
.iconos-nosotros h3 {
  text-transform: uppercase;
}

@media (min-width: 768px) {
  .contenedor-anuncios {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(3, 1fr);
  }
}
.anuncio {
  margin-top: 2rem;
  background-color: #eeeeee;
  border: 1px solid #c8c8c8;
}
.anuncio .contenido-anuncio {
  padding: 2rem;
}
.anuncio h3,
.anuncio p {
  margin: 0;
}
.anuncio .precio {
  color: #71B100;
  font-weight: bold;
  font-size: 2.5rem;
}
.anuncio a {
  display: block;
}

.iconos-caracteristicas {
  display: grid;
  gap: 2rem;
  grid-template-columns: repeat(3, 1fr);
  list-style: none;
  padding: 0;
}
.iconos-caracteristicas li {
  display: flex;
}
.iconos-caracteristicas li p {
  margin-left: 1rem;
  font-weight: 900;
}
.iconos-caracteristicas li img {
  width: 4rem;
  height: 4rem;
}

.ver-todas {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .ver-todas {
    display: flex;
    justify-content: right;
  }
}
.ver-todas .boton-verde {
  display: block;
}

.caracteristicas-propiedad {
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.caracteristicas-propiedad picture {
  max-width: 60rem;
}

.contactar {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 2rem;
  background-color: #2b2b2b;
  border-radius: 1rem;
  padding: 2rem;
}
.contactar p {
  width: 70%;
}
.contactar a {
  display: inline;
  border-radius: 1rem;
}
@media (min-width: 768px) {
  .contactar {
    flex-direction: row;
    justify-content: center;
  }
}

.webp .imagen-contacto {
  background-image: url("../img/encuentra.webp");
}

.no-webp .imagen-contacto {
  background-image: url("../img/encuentra.jpg");
}

.imagen-contacto {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  padding: 2rem;
  background-position: center center;
  background-size: cover;
  height: 35rem;
}
.imagen-contacto h2 {
  margin: 0;
}
.imagen-contacto p {
  margin: 2.5rem 2rem;
}
.imagen-contacto h2, .imagen-contacto p {
  color: #FFFFFF;
  text-align: center;
}

@media (min-width: 768px) {
  .seccion-inferior {
    display: grid;
    grid-template-columns: 2fr 1fr;
    gap: 3rem;
  }
}
.entrada-blog {
  margin-top: 2rem;
}
@media (min-width: 768px) {
  .entrada-blog {
    display: grid;
    grid-template-columns: 1fr 2fr;
    place-items: center;
    gap: 2rem;
  }
  .entrada-blog:last-of-type {
    margin-bottom: 0;
  }
}

.texto-entrada a {
  color: #000000;
}
.texto-entrada h4 {
  margin: 0;
}
.texto-entrada h4:after {
  display: block;
  content: "";
  width: 15rem;
  height: 0.5rem;
  background-color: #71B100;
  margin: 1rem 0;
}
.texto-entrada p {
  margin: 1.5rem 0;
}
.texto-entrada span {
  color: #E08709;
}

.testimoniales {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.testimonial {
  background-color: #71B100;
  border-radius: 1rem;
  padding: 2rem;
  color: #FFFFFF;
  position: relative;
}
.testimonial p {
  text-align: center;
  color: #FFFFFF;
  font-size: 1.8rem;
  font-weight: bold;
}
.testimonial p:before {
  content: "";
  display: block;
  height: 0.2rem;
  width: 100%;
  background-color: #FFFFFF;
  margin-bottom: 1.2rem;
}
@media (min-width: 768px) {
  .testimonial p {
    text-align: right;
  }
  .testimonial p:before {
    width: 60%;
    margin: 0 auto 1.2rem auto;
  }
}
.testimonial blockquote {
  padding-left: 5rem;
  font-size: 2rem;
}
.testimonial blockquote::before {
  content: "";
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  background-position: center center;
  height: 8rem;
  width: 6rem;
  display: block;
  position: absolute;
  left: 5rem;
  top: 1rem;
}

@media (min-width: 768px) {
  .sobre-nosotros {
    display: grid;
    gap: 2rem;
    grid-template-columns: repeat(2, 1fr);
    place-items: center;
  }
}
.sobre-nosotros h4 {
  text-align: center;
}

.formulario p {
  font-size: 1.4rem;
  color: #333333;
  margin: 2rem 0 0 0;
}
.formulario legend {
  font-size: 2rem;
  color: #333333;
}
.formulario label {
  font-weight: bold;
  text-transform: uppercase;
  display: block;
}
.formulario input:not([type=submit]), .formulario textarea, .formulario select {
  padding: 1rem;
  display: block;
  width: 100%;
  border: 1px solid #e1e1e1;
  border-radius: 1rem;
  margin-bottom: 2rem;
}
.formulario .forma-contacto {
  max-width: 30rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.formulario input[type=radio] {
  margin: 0;
  width: auto;
}
.formulario input[type=submit] {
  margin-top: 2rem;
  display: block;
  width: 100%;
}
@media (min-width: 768px) {
  .formulario input[type=submit] {
    width: auto;
  }
}

.alerta {
  padding: 0.5rem;
  text-align: center;
  color: #FFF;
  font-weight: bold;
  text-transform: uppercase;
  margin: 1rem 0;
}
.alerta.error {
  background-color: #ff2929;
}
.alerta.exito {
  background-color: #71B100;
  margin-bottom: 2rem;
}
.alerta.sesion {
  background-color: rgb(88, 88, 226);
}

table.propiedades {
  margin-top: 4rem;
  width: 100%;
  border-spacing: 0;
}
table.propiedades thead {
  background-color: #71B100;
}
table.propiedades thead td {
  color: #FFF;
  padding: 2rem;
}
table.propiedades .imagen-tabla {
  width: 10rem;
}

.small-img {
  width: 10rem;
}